<template>
  <div class="home">
    <div class="page flex"> 
      <div class="w-1/4"> 
      <ul class="card bg-gray-200 p-5 rounded-lg">
        <li class="p-2 m-1"><label for="name">Wall Tag</label><input type="text" v-model="wall.tag"></li>
        <li class="p-2 m-1"><label for="name">Length</label><input type="number" v-model="wall.length"></li>
        <li class="p-2 m-1"><label for="name">Height</label><input type="number" v-model="wall.height"></li>
          <li class="p-2 m-1"><label for="name">Thickness</label><input type="number" v-model="wall.thickness"></li>
          <li class="p-2 m-1"><label for="category">Category</label><input type="text" v-model="wall.category"></li> 
      <li class="p-2 m-1"><label for="name">Unit</label><input type="text" v-model="wall.unit"></li>    
      </ul>
      </div>
      <div class="w-1/2"> 
        <div class="bg-teal-200 text-2xl font-bold" v-if="wallArea">{{wallArea}} {{ru}}</div>
        <button @click="setRate">setRate</button>
          <table>
            <tr class="card bg-gray-300 p-5 mb-2" v-for="rate of jobrates" :key="rate.id">
              <td>{{rate.id}}</td>
              <td>{{rate.key.category}}</td>
              <td>{{rate.key.description}}</td>
              <td>{{rate.key.rate}}</td>

            </tr>
          </table>
          </div>
    </div>
    
  </div>
</template>

<script>
// @ is an alias to /src
import { mapGetters } from 'vuex'

export default {
  name: 'Home',
  components: {
   
  },
  data () {
    return {
      wall: {
        category: null,
        tag: null,
        unit: null,
        length: null,
        height: null,
        thickness: null,
        area: null,
        rate:null
      },
      ru: null



    }
  },
  mounted() {
    this.$store.dispatch('loadDayRates')
  },
  computed: {
    ...mapGetters({
      jobrates: 'jobrates'
    }),
    wallArea() {
      if(this.wall.unit == 'ft'){
        this.ru = 'ft2'
        this.wall.area = Math.round(this.wall.length * this.wall.height, 2)
        return this.wall.area
      } else {
        this.ru = 'm2'
        this.wall.area = Math.round((this.wall.length * this.wall.height) / 10.76, 2)
        return this.wall.area
      }
      
    }
  },
  methods: {
    async setRate() {
      const wdata = {
        category: this.wall.category,
        tag: this.wall.tag
      }
      this.wall.rate = this.searchRate(wdata)
      console.log(this.wall.rate)
    },
    async searchRate(wall) {
      this.jobrates.forEach(rate => {
        if(rate.category == wall.category && rate.description.includes(wall.tag)){
          return rate
        }  
        return 0      
      });
    }
  }

}
</script>
